import {
    FETCH_BERRIES,
    FETCH_MY_BERRIES,
    FETCH_SINGLE_BERRY,
    CATCH_BERRY,
    DELETE_BERRY,
    FETCH_ITEMS,
    FETCH_MY_ITEMS,
    FETCH_SINGLE_ITEM,
    CATCH_ITEM,
    DELETE_ITEM,
} from "../actions/types";

const initialState = {
  berries: [],
  myberries: [],
  berry: {},
  items: [],
  myitems: [],
  item: {}
};

function backpackReducer(state = initialState, action) {
  switch (action.type) {
    case FETCH_BERRIES:
      return { ...state, berries: action.payload };
    case FETCH_MY_BERRIES:
        return { ...state, myberries: action.payload };
    case FETCH_SINGLE_BERRY:
      return { ...state, berry: action.payload };
    case CATCH_BERRY:
        let berry = action.payload;
        let berriesList = state.myberries;
        berriesList.push(berry)
        return { ...state, myberries: berriesList }
    case DELETE_BERRY:
      let filteredlist = state.myberries.filter(
        (berry) => berry.id !== action.payload.id
      );
      return { ...state, myberries: filteredlist };
    case FETCH_ITEMS:
      return { ...state, items: action.payload };
    case FETCH_SINGLE_ITEM:
      return { ...state, item: action.payload };
    case FETCH_MY_ITEMS:
      return { ...state, myitems: action.payload };
    case DELETE_ITEM:
      let filteredItemsList = state.myitems.filter(
        (item) => item.id !== action.payload.id
      );
      return { ...state, myitems: filteredItemsList };
    case CATCH_ITEM:
      let item = action.payload;
      let myItemsList = state.myitems;
      myItemsList.push(item)
      return { ...state, myitems: myItemsList }
    default:
      return state;
  }
}

export default backpackReducer;
