import React, { Component } from "react";
import { connect } from "react-redux";
import { fetchMyPokemon, deletePokemon } from "../actions/pokeActions";
import PokemonCard from "./PokemonCard";

class MyPokemonContainer extends Component {
  componentDidMount() {
    this.props.fetchMyPokemon();
  }
  componentWillUnmount(){
    // console.log('MYPOKEMONCONTAINER is unmounted.')
  }
  
  render() {

    return (
      <>
        <h1>Pokedex</h1>
        <div
          className="container"
          style={{ display: "flex", flexWrap: "wrap" }}
        >
          {
            this.props.mypokemon.map((pokemon) => {
              return (
                <PokemonCard
                  key={pokemon.id}
                  pokemon={pokemon}
                  onDelete={this.props.deletePokemon}
                />
              )
            })
          }
        </div>
      </>
    );
  }
}

const mapStateToProps = (state) => ({
  mypokemon: state.combineRed.mypokemon,
});

const mapDispatchToProps = (dispatch) => {
  return {
    fetchMyPokemon: (pokemon) => dispatch(fetchMyPokemon(pokemon)),
    deletePokemon: (pokemon) => dispatch(deletePokemon(pokemon)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(MyPokemonContainer);
