import React, { Component } from "react";
import { connect } from "react-redux";
import { fetchMyBerries, deleteBerry } from "../actions/backpackActions";
import ViewBerry from "./ViewBerry";

class BerriesContainer extends Component {
  componentDidMount() {
    this.props.fetchMyBerries();
  }

  componentWillUnmount() {
    console.log("BERRIESCONTAINER has unmounted.");
  }

  render() {
    return (
      <>
        <h1>My Berries</h1>
        {this.props.berries.map((berry) => {
          return (
            <ViewBerry
              key={berry.id}
              berry={berry}
              onDelete={this.props.deleteBerry}
            />
          );
        })}
      </>
    );
  }
}

const mapStateToProps = (state) => ({
  berries: state.combineRedTwo.myberries,
});
const mapDispatchToProps = (dispatch) => {
  return {
    fetchMyBerries: (berry) => dispatch(fetchMyBerries(berry)),
    deleteBerry: (berry) => dispatch(deleteBerry(berry)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(BerriesContainer);
