import React, { Component } from "react";
import { connect } from "react-redux";
import { fetchSinglePokemon } from "../actions/pokeActions";
import { Link } from "react-router-dom";
import Button from "./Button";

class ViewPokemon extends Component {
  constructor() {
    super();
    this.state = {
      toggled: true,
    };
  }
  handleClick = () => {
    this.setState((previousState) => {
      return {
        toggled: !previousState.toggled,
      };
    });
  };
  componentDidMount() {
    this.props.fetchSinglePokemon(this.props.pokeId);
  }
  componentWillUnmount() {
    // console.log("VIEWPOKEMON has unmounted.");
  }
  render() {
    if (!this.props.singleMon.id) {
      return <p>"Not found "</p>;
    }
    // { this.props.singleMon.held_items[0].item.name }
    return (
      <>
        <br />
        <label>
          {this.state.toggled ? (
            <img src={this.props.singleMon.sprites.front_default} alt="" />
          ) : (
            <img src={this.props.singleMon.sprites.back_default} alt="" />
          )}
        </label>
        <br />
        <Button type="button" onClick={this.handleClick}>
          {" "}
          Change View{" "}
        </Button>
        <br />
        <label>Name: {this.props.singleMon.name}</label>
        <br />
        <label>Type: {this.props.singleMon.types[0].type.name}</label>
        <br />
        <label>Ability: {this.props.singleMon.abilities[0].ability.name}</label>
        <br />
        <label>Move: {this.props.singleMon.moves[0].move.name}</label>
        <br />
        <label>Base Experience: {this.props.singleMon.base_experience}</label>
        <br />
        <label>Form: {this.props.singleMon.forms[0].name}</label>
        <br />
        <label>
          Version: {this.props.singleMon.game_indices[0].version.name}
        </label>
        <br />
        <label>Height: {this.props.singleMon.height}</label>
        <br />
        <Link className="Nav__" to="/pokedex">
          {" "}
          Pokedex{" "}
        </Link>
      </>
    );
  }
}

const mapStateToProps = (state) => {
  return { 
    singleMon: state.combineRed.singleMon 
  };
};
const mapDispatchToProps = (dispatch) => {
  return {
    fetchSinglePokemon: (berry) => dispatch(fetchSinglePokemon(berry)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(ViewPokemon);
