import {
  FETCH_BERRIES,
  FETCH_MY_BERRIES,
  FETCH_SINGLE_BERRY,
  CATCH_BERRY,
  DELETE_BERRY,
  FETCH_ITEMS,
  FETCH_MY_ITEMS,
  FETCH_SINGLE_ITEM,
  CATCH_ITEM,
  DELETE_ITEM,
} from "./types";

let BERRY_URL = "https://pokeapi.co/api/v2/berry";
let MY_BERRIES = "http://localhost:3001/berries";
let MY_ITEMS = "http://localhost:3001/items";
let ITEMS_URL = "https://pokeapi.co/api/v2/item/";

// Berry
export const fetchBerries = () => (dispatch) => {
  fetch(BERRY_URL)
    .then((response) => response.json())
    .then((berry) =>
      dispatch({
        type: FETCH_BERRIES,
        payload: berry,
      })
    );
};
export const fetchMyBerries = (berryData) => (dispatch) => {
  fetch(MY_BERRIES)
    .then((response) => response.json())
    .then((berry) => dispatch({ type: FETCH_MY_BERRIES, payload: berry }));
};
export const fetchSingleBerry = (id) => (dispatch) => {
  fetch(`${MY_BERRIES}/${id}`)
    .then((response) => response.json())
    .then((item) =>
      dispatch({
        type: FETCH_SINGLE_BERRY,
        payload: item,
      })
    );
};
export const newBerry = (berryData) => (dispatch) => {
  // console.log(dispatch);
  fetch(MY_BERRIES, {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
    },
    body: JSON.stringify(berryData),
  })
    .then((response) => response.json())
    .then((berry) => {
      // console.log(berry);
      return dispatch({
        type: CATCH_BERRY,
        payload: berry,
      });
    });
};
export const deleteBerry = (id) => (dispatch) => {
  // console.log(dispatch);
  fetch(`${MY_BERRIES}/${id}`, {
    method: "DELETE",
    headers: {
      "Content-Type": "application/json",
    },
    body: JSON.stringify({ id }),
  })
    .then((response) => response.json())
    .then((berry) => {
      return dispatch({
        type: DELETE_BERRY,
        payload: berry,
      });
    });
};
