import React, { Component } from "react";
import { connect } from "react-redux";
import { fetchMyItems, deleteItem } from "../../actions/backpackActions";
import MySingleItem from "./MySingleItem";

class MyItems extends Component {
  componentDidMount() {
    this.props.fetchMyItems();
  }

  componentWillUnmount() {
    // console.log("ITEMSCONTAINER has unmounted.");
  }

  render() {
    return (
      <>
        <h1>My Items</h1>
        {this.props.items.map((item) => {
          return (
            <MySingleItem
              key={item.id}
              item={item}
              onDelete={this.props.deleteItem}
            />
          );
        })}
      </>
    );
  }
}

const mapStateToProps = (state) => ({
  items: state.combineRedTwo.myitems,
});
const mapDispatchToProps = (dispatch) => {
  return {
    fetchMyItems: (item) => dispatch(fetchMyItems(item)),
    deleteItem: (item) => dispatch(deleteItem(item)),
  };
};
//if its not a function and inside curlies, it returns an object
export default connect(mapStateToProps, mapDispatchToProps)(MyItems);
