import React from 'react'
import { v4 as uuidv4 } from "uuid";
import Button from '../../components/Button'


const MySingleItem = props => {
    console.log(props)
    return (
        <div key={uuidv4()} className="item">
            <h4>{props.item.name}</h4>
            <p>
            Amount:{" "}{props.item.amount}{" "}
            CardText:{" "}{props.item.cardtext}{" "}
            EReaderData:{" "}{props.item.ereaderData}{" "}
            ParentToChild:{" "}{props.item.parentToChild}{" "}
            Print:{" "}{props.item.print}{" "}
            Release Information:{" "}{props.item.releaseInformation}{" "}
            Sibling Number:{" "}{props.item.siblingNumber}{" "}
            <Button onClick={() => props.onDelete(props.item.id)}>Delete</Button>
            </p>
        </div>
    )
}

export default MySingleItem