import React from "react";
import Button from '../components/Button';
import { v4 as uuidv4 } from "uuid";

const ViewBerry = props => {
    return (
        <div key={uuidv4()} className="berry">
            <h4>{props.berry.name}</h4>
            <p>
                Amount:{" "}{props.berry.amount}{" "}
                Growth Time:{props.berry.growthTime}{" "}
                Max Harvest:{props.berry.maxHarvest}{" "}
                Natural Gift Power:{props.berry.naturalGiftPower}{" "}
                Size:{props.berry.size}{" "}
                Smoothness:{props.berry.smoothness}{" "}
                Soil Dryness:{props.berry.soilDryness}{" "}
                Firmness:{props.berry.firmness}{" "}
                Potency:{props.berry.potency}{" "}
                Flavor:{props.berry.flavor}{" "}
                Natural Gift Type:{props.berry.naturalGiftType}{" "}
                Sibling Number:{props.berry.siblingNumber}{" "}
                Parent To Child:{props.berry.parentToChild}{" "}
                {props.berry.id}{" "}
                <Button onClick={() => props.onDelete(props.berry.id)}>Delete</Button>
            </p>
        </div>
    )
}

export default ViewBerry;
