import React, { PureComponent } from "react";
import { connect } from "react-redux";
import { fetchBerries, newBerry } from "../actions/backpackActions";
import "../App.css";
import BerryForm from "./BerryForm";
// Itemsf
import Items from "./Items/Display/Items";

class BackpackComponentBerries extends PureComponent {
  // We use the constructor to set the INITIAL STATE We call super in the constructor since we are inheriting from another class via
  // the extends keyword.
  constructor() {
    super();
    this.state = {
      name: "",
      amount: 0,
      count: 0,
      siblingNumber: 0,
      parentToChild: 1,
      toggled: false,
      color: "",
      growthTime: "",
      maxHarvest: "",
      naturalGiftPower: "",
      size: 0,
      smoothness: 0,
      soilDryness: 0,
      firmness: "",
      potency: 0,
      flavor: "",
      naturalGiftType: "",
    };
  }

  //   our add methods makes use of the 'setState' method, which is whay we use to alter state
  add = () => {
    let newAmount = this.state.amount + 1;
    console.log(`Before : ${this.state.amount}`);
    this.setState({ amount: newAmount }, () => {
      console.log(`Inside: ${this.state.amount}`);
    });
    console.log(`After: ${this.state.amount}`);
  };
  subtract = () => {
    let lessAmount = this.state.amount - 1;
    console.log(`Before: ${this.state.amount}`);
    this.setState({ amount: lessAmount }, () => {
      console.log(`Inside: ${this.state.amount}`);
    });
    console.log(`After: ${this.state.amount}`);
  };
  reset = () => {
    console.log(`Before: ${this.state.amount}`);
    this.setState({ amount: 0 }, () => {
      console.log(`Inside: ${this.state.amount}`);
    });
    console.log(`After: ${this.state.amount}`);
  };
  handleChange = (event) => {
    console.log(`Before: `,this.state)
    this.setState({ [event.target.name]: event.target.value }, () => {
      console.log(`Inside: `,this.state);
    });
    console.log(`After: `,this.state)
  };
  handleCallbackOne = (event) => {
    console.log(event.target.value);
    this.setState({
      name: event.target.value,
    });
  };
  handleSiblingNumber = () => {
    this.setState({
      siblingNumber: this.state.siblingNumber + 1,
    });
  };
  handleParentToChild = (event) => {
    this.setState({
      parentToChild: this.state.parentToChild + 3,
    });
  };
  handleClick = () => {
    this.setState((previousState) => {
      return {
        toggled: !previousState.toggled,
      };
    });
  };
  handleSubmit = (event) => {
    event.preventDefault();
    console.log("handleSubmit", " We will make a POST request here");
    const berry = {
      name: this.state.name,
      amount: this.state.amount,
      growthTime: this.state.growthTime,
      maxHarvest: this.state.maxHarvest,
      naturalGiftPower: this.state.naturalGiftPower,
      size: this.state.size,
      smoothness: this.state.smoothness,
      soilDryness: this.state.soilDryness,
      firmness: this.state.firmness,
      potency: this.state.potency,
      flavor: this.state.flavor,
      naturalGiftType: this.state.naturalGiftType,
      siblingNumber: this.state.siblingNumber,
      parentToChild: this.state.parentToChild,
    };
    console.log(berry);
    this.props.newBerry(berry);
  };
  changeCardColor = () => {
    this.setState({
      color: "blue",
    });
  };
  // Lifecycle
  static getDerivedStateFromProps(state) {
    console.log("getDerivedStateFromProps");
    return {
      color: "#FFFFFF",
    };
  }
  componentDidMount() {
    // this.props.fetchItems();
    this.props.fetchBerries();
    // updater, [callback]
  }
  // getSnapShotBeforeUpdate - called just before React updates and commits new content to the DOM - used
  // rarely. getSnapShotBeforeUpdate can capture data that maybe changing rapidly.
  // getSnapshotBeforeUpdate() {
  //     console.log('Amount', 'getSnapshotBeforeUpdate')
  // }
  // componentDidUpdate is useful for DOM manipulation and updating 3rd party libraries.
  componentDidUpdate() {
  }
  componentWillUnmount() {
    clearInterval(this.interval);
  }
  // Math.floor(Math.random() * 16777215).toString(16)
  // Item Amount Functions
  amountTick = () => {
    this.setState({
      amount: this.state.amount + 1,
    });
  };
  stopAmount = () => {
    clearInterval(this.interval);
  };
  startAmount = () => {
    this.interval = setInterval(this.amountTick, 1000);
  };

  render() {
    if (!this.props.berries.results) {
      return <p>Berries Not Found</p>;
    }

    let { color } = this.state;
    console.log(color);

    return (
      <>
        <BerryForm
          name={this.state.name}
          amount={this.state.amount}
          growthTime={this.state.growthTime}
          maxHarvest={this.state.maxHarvest}
          naturalGiftPower={this.state.naturalGiftPower}
          size={this.state.size}
          smoothness={this.state.smoothness}
          soilDryness={this.state.soilDryness}
          firmness={this.state.firmness}
          potency={this.state.potency}
          flavor={this.state.flavor}
          naturalGiftType={this.state.naturalGiftType}
          siblingNumber={this.state.siblingNumber}
          parentToChild={this.state.parentToChild}
          onSubmit={this.handleSubmit}
          onChange={this.handleChange}
          handleParentToChild={this.handleParentToChild}
        />
        <br />
        <section
          className="Amount"
          style={{
            backgroundColor: "white",
            borderRadius: "8px",
          }}
        ></section>
        <br />
        <Items
          items={this.props.berries.results}
          handleCallbackOne={this.handleCallbackOne}
          handleSiblingNumber={this.handleSiblingNumber}
          siblingNumber={this.state.siblingNumber}
          parentToChild={this.state.parentToChild}
        />
      </>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    berries: state.combineRedTwo.berries,
  };
};
const mapDispatchToProps = (dispatch) => {
  return {
    fetchBerries: (berry) => dispatch(fetchBerries(berry)),
    newBerry: (berry) => dispatch(newBerry(berry)),
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(BackpackComponentBerries);

