import React from "react";
import Button from "../../../components/Button";

const Item = (props) => {
  return (
    <>
      {props.name}
      <Button onClick={props.handleCallbackOne} value={props.name}>
        Choose Item
      </Button>
      <Button onClick={props.handleSiblingNumber}>Level Up</Button>
      {props.siblingNumber} <label>HP Up: </label>
      {props.parentToChild}
    </>
  );
};
export default Item;

